import PropTypes from "prop-types";
import React from "react";

const propTypes = {
  actions: PropTypes.object,
  player: PropTypes.object,
};
export default class ForwardControl extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { player } = this.props;
    return (
      <button
        className={classNames(
          {
            "ekiio-video-icon-fullscreen-exit": player.isFullscreen,
            "ekiio-video-icon-fullscreen": !player.isFullscreen,
          },
          "video-fullscreen-control video-control ekiio-video-icon",
          "tool-tip"
        )}
        ref={(c) => {
          this.button = c;
        }}
        tabIndex="0"
        onClick={this.handleClick}
      >
        <div className="tool-tip-text top-left">
          {this.props.player.isFullscreen
            ? "Thoát chế độ toàn màn hình"
            : "Toàn màn hình"}
        </div>
      </button>
    );
  }
}
